@import 'src/styles/partials/breakpoints';

$grey: #595959;
$yellow: #FCEB55;

.steps-action {
    display: flex;
    align-items: center;
    @media screen and ( max-width: $screen-sm-min ) {
        flex-direction: column-reverse; } }

.justify-content-space-between {
    justify-content: space-between; }

.justify-content-center {
    justify-content: center; }

.small-card {
    width: 12rem;
    height: 3.2rem;
    border-radius: .5rem; }
.fancy-card {
    @extend .small-card;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    cursor: pointer;
    & > p {
        padding: 0;
        margin: 0;
        font-size: 1rem;
        font-weight: bold; } }

.fancy-card-selected {
    border-color: #1eff4760;
    border-width: 2px;
    border-style: solid; }

.multi-step-form-button {
    @extend .small-card;
    // @media screen and ( max-width: $screen-sm-min )
 }    // // width: 4rem

.yellow-button {
   background-color: $yellow;
   color: $grey;
   &.ant-btn:focus, .ant-btn.focus {
       background-color: $yellow;
       color: $grey; }
   &.ant-btn:hover {
       background: $yellow;
       color: $grey;
       border-color: transparent; } }

.grey-button {
    background-color: $grey;
    color: $yellow;
    &.ant-btn:focus, .ant-btn.focus {
       background-color: $grey;
       color: $yellow; }
    &.ant-btn:hover {
        background: $grey;
        color: $yellow;
        border-color: transparent; } }




.ant-btn:focus, .ant-btn.focus {
    -webkit-animation: none !important;
    -moz-animation: none !important;
    -o-animation: none !important;
    -ms-animation: none !important;
    animation: none !important;
    border-color: transparent; }
